<template>
  <div id="root">
    <p style="font-size:16px">先扫MP，再扫魔方</p>
    <div style="float:left;margin-left:5%;text-align:center;font-size:20px"  v-for="(item,index) in src" :key="index">
      <img style="width:90%;height:90%" :src="item.src" >
      
        <p>{{item.name}}</p>
    </div>
  </div>
</template>src
<script>
export default {
     data() {
    return {
      src:[],
      qsrc:"",
      wsrc:""
     
      
    };
  },
  created() {
    var url = "http://192.168.146.159:3000/getqrcode" ;
    this.$http
      .get(url)
      .then(res => {
      
      this.src=[];
      for (var i=0;i<res.data.length;i++){
            this.src.push({src:"http://192.168.146.159:3000/public/images/"+res.data[i]+"",name:res.data[i].split(".")[0]})
         
      }
      
      })
      .catch(err => {});
  }
}
 
</script>

<style>
#root {
  width: 80%;
  height: 100%;
  margin-top: 70px;
  margin-left: 15%;
  float: left;
}
</style>



