          key: "消耗",
          align: "center"
        },
        {
          title: "曝光",
          key: "曝光",
          align: "center"
        },
        {
          title: "点击",
          key: "点击",
          align: "center"
        },
        {
          title: "点击率",
          key: "点击率",
          align: "center"
        },
        {
          title: "新增（授权前）",
          key: "新增（授权前）",
          width: 150,
          height: 400,
          align: "center",
          render: (h, params) => {
            return h("div", [
              h("span", { style: {} }, params.row["新增（授权前）"]),
