            ]);
          }
        },
        {
          title: "新增（授权后）",
          key: "新增（授权后）",
          align: "center",
          sortable: true,
          width: 150,
          render: (h, params) => {
            return h("div", [
              h("span", { style: {} }, params.row["新增（授权后）"]),
              h("span", { style: { color: "gray" } }, params.row["newzc"]),
              h("p", { style: { color: "gray" } }, params.row["xzdjzc"])
            ]);
          }
        },
        {
          title: "授权转化",
          key: "授权转化",
          align: "center"
        },
        {
          title: "开局账号数",
          key: "开局账号数",
          align: "center"
        },
        {
