              h("p", { style: {} }, params.row["argclickrate"]),
              
            ]);
          }
        },
        {
          key: "before",
          width: 150,
          align: "center",
          render: (h, params) => {
          return h("div", [
            h("p", { style: {} }, params.row["before"]),
         
              
            ]);
          }
        },
        {
          key: "after",
          width: 150,
          align: "center",
          render: (h, params) => {
          return h("div", [
            h("p", { style: {} }, params.row["after"]),
           
              
            ]);
          }
        },
        {
          key: "Dofauthority",
          align: "center",
             render: (h, params) => {
          return h("div", [
           
            h("p", { style: {} }, params.row["argDofauthority"]),
              
            ]);
          }
        },
        {
          key: "OpeningNumbe",
          align: "center",
             render: (h, params) => {
          return h("div", [
            h("p", { style: {} }, params.row["OpeningNumbe"]),
            
              
            ]);
          }
        },
        {
          key: "Openingrate",
          align: "center",
             render: (h, params) => {
          return h("div", [
            h("p", { style: {} }, params.row["argOpeningrate"]),
              
            ]);
          }
        },
        {
          key: "Perinventory",
          align: "center",
             render: (h, params) => {
          return h("div", [
          
            h("p", { style: {} }, params.row["argPerinventory"]),
              
            ]);
          }
        },
         {
          key: "date",
          align: "center"
        }
      ],
      data1: [
        {
          Channelnumber: "总结\\平均",
          Channelname: "MP",
          source: "",
          consume: "",
          exposure: "",
          tap: "",
          clickrate: "",
          before: "",
          after: "",
          Dofauthority: "",
          OpeningNumbe: "",
          Openingrate: "",
          Perinventory: "",
          date:"一一"
        }
      ],
      returnlist: []
    };
  },
  methods: {
    submit() {
      
      var qudaoList = [];
      if (this.select == "Exclude") {
        for (var i = 0; i < this.channelList.length; i++) {
          qudaoList.push(this.channelList[i].label);
        }

        for (var i = 0; i < qudaoList.length; i++) {
          for (var j = 0; j < this.modelqudao.length; j++) {
            if (qudaoList[i] == this.modelqudao[j]) {
              qudaoList.splice(i, 1);
            }
          }
        }
        if (qudaoList.length == 0) {
          qudaoList = "";
        }
      } else {
        if (this.modelqudao.length) {
          qudaoList = this.modelqudao;
        } else {
          qudaoList = "";
        }
      }
      var url = "http://192.168.146.159:3000/find?";
      let postData = qs.stringify({
        projiectName: this.proname,
        date: this.datevalue,
        qudao: qudaoList
      });

      this.$http
        .get(url + postData)
        .then(res => {
          var totalconsume=0;
          var exposure=0;
          var clcik=0;
          var clickrate=0;
          var after=0;
          var before=0;
          var shouquan=0;
          var kaiju=0;
          var kajurare=0;
          var renjun=0;
          var newsblength=0;
          var kaijulength=0;
          var renjunlength=0;
          this.data = [];
          this.returnlist = [];
          this.returnlist = res.data;
         
          for (var i = 0; i < 10; i++) {
            if (this.returnlist[i]) {
              this.data.push(this.returnlist[i]);
            }
          }
          for (j=0;j<res.data.length;j++){
            
             var click = parseFloat(res.data[j]["点击"].split(",").join(""));
             var consume = parseFloat(res.data[j]["消耗"]);
             totalconsume+=parseFloat(res.data[j]['消耗']);
             exposure+=parseFloat(res.data[j]['曝光'].split(",").join(""));
             clcik+=parseFloat(res.data[j]['点击'].split(",").join(""));
             clickrate+=parseFloat(res.data[j]["点击率"].split("%")[0]);
             if(res.data[j]["新增（授权前）"]&&res.data[j]["新增（授权前）"]!="一一"){
              newsblength++;
              before+=parseFloat(res.data[j]["新增（授权前）"]);
              var newsb =parseFloat( res.data[j]["新增（授权前）"].split(",").join(""));
              var zhlsb = (newsb / click).toFixed(2) + "%";
              var xzdjsb = "￥" + "(" + (consume / newsb).toFixed(2) + ")";
              res.data[j]["newsb"] = "(" + zhlsb + ")";
              res.data[j]["xzdjsb"] = xzdjsb;
             }
            else{
                res.data[j]["newsb"] = "";
                res.data[j]["xzdjsb"] = "";
            }
            if(res.data[j]["新增（授权后）"]&&res.data[j]["新增（授权后）"]!="一一"){
              
              after+=parseFloat(res.data[j]["新增（授权后）"]);
              var newzc = parseFloat(res.data[j]["新增（授权后）"].split(",").join(""));
              var xzdjzc = "￥" + "(" + (consume / newzc).toFixed(2) + ")";
              var zhlzc = (newzc / click).toFixed(2) + "%";
              res.data[j]["newzc"] = "(" + zhlzc + ")";
              res.data[j]["xzdjzc"] = xzdjzc;

            }
             else{
                res.data[j]["newzc"] = "";
                res.data[j]["xzdjzc"] = "";
              }
            if(res.data[j]['授权转化']&&res.data[j]['授权转化']!="一一"){
               shouquan+=parseFloat( res.data[j]['授权转化'].split("%")[0]);
               
             }
            if(res.data[j]['开局账号数']&&res.data[j]['开局账号数']!="一一"&&res.data[j]['开局账号数']!=""){
              kaijulength++;
              kaiju+=parseFloat( res.data[j]['开局账号数']);
             
             }
            if(res.data[j]['人均盘数']&&res.data[j]['人均盘数']!="一一"){
              renjunlength++
              
              renjun+=parseFloat(res.data[j]['人均盘数']);
               
             }
              if(res.data[j]['开局率']&&res.data[j]['开局率']!="一一"){
              
              kajurare+=parseFloat( res.data[j]['开局率'].split("%")[0]);
              
             }
           
          
          
          }
          
          this.data1[0].consume=totalconsume.toFixed(2);
          this.data1[0].exposure=exposure.toFixed(2);
          this.data1[0].tap=clcik.toFixed(2);
          this.data1[0].argclickrate=(clickrate/res.data.length).toFixed(2)+"%";
          this.data1[0].after=after.toFixed(2);
          this.data1[0].before=before.toFixed(2);
          this.data1[0].argDofauthority=(shouquan/newsblength).toFixed(2)+"%";
          this.data1[0].OpeningNumbe=kaiju.toFixed(2);
          if(kaijulength==0){
            this.data1[0].argOpeningrate="0.00%"
          }
          else{
            this.data1[0].argOpeningrate=(kajurare/kaijulength).toFixed(2)+"%";
          }
           
          this.data1[0].argPerinventory=(renjun/renjunlength).toFixed(2);
          this.pagenum = Math.ceil(this.returnlist.length);

          
        })
        .catch(err => {
          console.log(err);
        });
    },
    changeDate(date) {
      this.datevalue = date;
    },
    changePage(page) {
      this.data = [];
      for (var i = (page - 1) * 10; i < page * 10; i++) {
        if (this.returnlist[i]) {
          var click = parseFloat(this.returnlist[i]["点击"].split(",").join(""));
          var consume = parseFloat(this.returnlist[i]["消耗"]);
          if( this.returnlist[i]["新增（授权前）"]&& this.returnlist[i]["新增（授权前）"]!="一一"){
                var newsb =parseFloat( this.returnlist[i]["新增（授权前）"].split(",").join(""));
                var zhlsb = (newsb / click).toFixed(2) + "%";
                var xzdjsb = "￥" + "(" + (consume / newsb).toFixed(2) + ")";
                this.returnlist[i]["newsb"] = "(" + zhlsb + ")";
                this.returnlist[i]["xzdjsb"] = xzdjsb;
                }
              else{
                this.returnlist[i]["newsb"] = "";
                this.returnlist[i]["xzdjsb"] = "";
              }
              if( this.returnlist[i]["新增（授权后）"]&& this.returnlist[i]["新增（授权后）"]!="一一"){
                var newzc = parseFloat(this.returnlist[i]["新增（授权后）"].split(",").join(""));
                var xzdjzc = "￥" + "(" + (consume / newzc).toFixed(2) + ")";
                var zhlzc = (newzc / click).toFixed(2) + "%";
                this.returnlist[i]["newzc"] = "(" + zhlzc + ")";
                this.returnlist[i]["xzdjzc"] = xzdjzc;
                
              }
              else{
                this.returnlist[i]["newzc"] = "";
                this.returnlist[i]["xzdjzc"] = "";
              }
            
          this.data.push(this.returnlist[i]);
        }
      }
    },
    download() {
      let str = `项目名,渠道号,渠道名,消耗,曝光,点击,点击率,新增（授权前）,新增（授权后）,授权转化,开局账号数,开局率,人均盘数,日期\n`;
      //增加\t为了不让表格显示科学计数法或者其他格式
      for(let i = 0 ; i < this.exceljson.length ; i++ ){
        for(let item in this.exceljson[i]){
          if(item=="素材"||item=="newsb"||item=="xzdjsb"||item=="newzc"||item=="xzdjzc"){
            continue;
          }
          if(item=="曝光"||item=="点击"||item=="消耗"){
            this.exceljson[i][item]=this.exceljson[i][item].split(",").join("")
          }
             str+=`${this.exceljson[i][item] + '\t'},`; 
            // console.log(this.exceljson[i][item])    
        }
         str+='\n';
      }
      
      //encodeURIComponent解决中文乱码
      let uri = 'data:text/csv;charset=utf-8,\ufeff' + encodeURIComponent(str);
      //通过创建a标签实现
      var link = document.createElement("a");
      link.href = uri;
      //对下载的文件命名
      link.download =  "数据表.csv";
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    
    },
    init() {
      var url = "http://192.168.146.159:3000/init?" + document.cookie;
      this.$http
        .get(url)
        .then(res => {
          this.exceljson=res.data;
          var qudaolist = [];
          var pnamelist = [];
          this.proname = res.data.pop();
          var length = res.data.length;
          var totalconsume = 0;
          var totalbbaoguang = 0;
          var totalclcik = 0;
          var totalclickrate=0;
          var totalnewsb=0;
          var totalnewzc=0;
          var newsblength=0;
          var Dofauthority=0;
          var OpeningNumbe=0;
          var Openingrate=0;
          var Perinventory=0;
          var kaijulength=0;
          var renjunlength=0
          for (var i = 0; i < length; i++) {
             for (var  j in res.data[i])
              {
                if(!res.data[i][j]){
                res.data[i][j]="一一"
                }
              }
            if(res.data[i]["新增（授权前）"]&&res.data[i]["新增（授权前）"]!="一一"){
              newsblength++;
              totalnewsb+=parseFloat(res.data[i]["新增（授权前）"]);
            }
            if(res.data[i]["新增（授权后）"]&&res.data[i]["新增（授权后）"]!="一一"){
              
              totalnewzc+=parseFloat(res.data[i]["新增（授权后）"]);

            }
            if (qudaolist.indexOf(res.data[i]["渠道号"]) == -1) {
              qudaolist.push(res.data[i]["渠道号"]);
            }
            if (pnamelist.indexOf(res.data[i]["项目名"]) == -1) {
              pnamelist.push(res.data[i]["项目名"]);
            }
            totalconsume += parseFloat(res.data[i]["消耗"]);
            totalbbaoguang += parseFloat(
              res.data[i]["曝光"].split(",").join("")
            );
            totalclcik += parseFloat(res.data[i]["点击"]);
            totalclickrate+=parseFloat(res.data[i]["点击率"].split("%")[0]);
            
           
            // newsb+=parseFloat(res.data[i]["新增（授权前）"]);
          }
          this.data1[0].after=totalnewzc.toFixed(2);
          this.data1[0].before=totalnewsb.toFixed(2);
          this.data1[0].tap = totalclcik.toFixed(2);
          this.data1[0].exposure = totalbbaoguang.toFixed(2);
          this.data1[0].consume = totalconsume.toFixed(2);
          this.data1[0].clickrate=totalclickrate.toFixed(2)+"%";
          this.data1[0].argclickrate=(totalclickrate/length).toFixed(2)+"%";
       
          for (var i = 0; i < qudaolist.length; i++) {
            this.channelList.push({
              value: qudaolist[i],
              label: qudaolist[i]
            });
          }
          for (var i = 0; i < pnamelist.length; i++) {
            this.projectList.push({
              value: pnamelist[i],
              label: pnamelist[i]
            });
          }
          this.returnlist = res.data;

          for (var i = 0; i < 10; i++) {
          
            if (res.data[i]) {
              var click = parseFloat(this.returnlist[i]["点击"].split(",").join(""));
              var consume = parseFloat(this.returnlist[i]["消耗"]);
              if( this.returnlist[i]["新增（授权前）"]&& this.returnlist[i]["新增（授权前）"]!="一一"){
                var newsb =parseFloat( this.returnlist[i]["新增（授权前）"].split(",").join(""));
                var zhlsb = (newsb / click).toFixed(2) + "%";
                var xzdjsb = "￥" + "(" + (consume / newsb).toFixed(2) + ")";
                this.returnlist[i]["newsb"] = "(" + zhlsb + ")";
                this.returnlist[i]["xzdjsb"] = xzdjsb;
                }
              else{
                this.returnlist[i]["newsb"] = "";
                this.returnlist[i]["xzdjsb"] = "";
              }
              if( this.returnlist[i]["新增（授权后）"]&& this.returnlist[i]["新增（授权后）"]!="一一"){
                var newzc = parseFloat(this.returnlist[i]["新增（授权后）"].split(",").join(""));
                var xzdjzc = "￥" + "(" + (consume / newzc).toFixed(2) + ")";
                var zhlzc = (newzc / click).toFixed(2) + "%";
                this.returnlist[i]["newzc"] = "(" + zhlzc + ")";
                this.returnlist[i]["xzdjzc"] = xzdjzc;
                
              }
              else{
                this.returnlist[i]["newzc"] = "";
                this.returnlist[i]["xzdjzc"] = "";
              }
              this.data.push(res.data[i]);
            }
          }
          
          for (j in res.data){
             if(res.data[j]['授权转化']&&res.data[j]['授权转化']!="一一"){
               Dofauthority+=parseFloat( res.data[j]['授权转化'].split("%")[0]);
               
             }
           
             if(res.data[j]['开局账号数']=="0"){
               res.data[j]["开局率"]="一一";
               res.data[j]["开局账号数"]="一一";
             }
            if(res.data[j]['开局账号数']&&res.data[j]['开局账号数']!="一一"){
              kaijulength++;
              OpeningNumbe+=parseFloat( res.data[j]['开局账号数']);
             
             }
            if(res.data[j]['人均盘数']&&res.data[j]['人均盘数']!="一一"){
              renjunlength++
              Perinventory+=parseFloat( res.data[j]['人均盘数']);
               
             }
              if(res.data[j]['开局率']&&res.data[j]['开局率']!="一一"){
              
              Openingrate+=parseFloat(res.data[j]['开局率'].split("%")[0]);
              
             }
             if(res.data[j]['开局账号数']=="一一"){
               res.data[j]['开局率']="一一"
             }
             
          }
          
          
          this.data1[0].argOpeningrate=(Openingrate/kaijulength).toFixed(2)+"%";
          this.data1[0].Dofauthority=Dofauthority.toFixed(2)+"%";
          this.data1[0].argDofauthority=(Dofauthority/newsblength).toFixed(2)+"%";
          this.data1[0].OpeningNumbe=OpeningNumbe.toFixed(2);
          this.data1[0].Perinventory=Perinventory.toFixed(2);
         
          this.data1[0].argPerinventory=(Perinventory/renjunlength).toFixed(2);
          this.pagenum = Math.ceil(res.data.length);
        })
        .catch(err => {
          console.log(err);
        });
    }
  },
  created() {
    this.init();
  }
};
</script>

<style>
#app {
  text-align: center;
  margin-top: 70px;
  
}
#select{
  width: 85%;
  margin: auto;
  float: left;
  margin-left: 13%;
  height: auto;
}
.option {
  margin-left: 1%;
  text-align-last: center;
  width: 25%;
  text-align: center;
  float: left;
  
}

.export {
  float: right;
  margin-right: 5%;
}
.datepicker {
  float: left;
  width: 25%;
  margin-left: 1%;
}
.showarea {
  width: 85%;
  margin: auto;
  margin-top: 1%;
  margin-left: 13%;
  float: left;
}
.channel {
 
  float: left;
  width: 25%;
  margin-left: 1%;
}
.page {
  margin-top: 1%;
}
</style>



