<template>
  <div>
    <router-view></router-view>
   
    <div class="menu">
      <Menu
        :theme="theme2"
        width="auto"
        mode="vertical"
        @on-select="select"
        @on-open-change="opchange"
        :accordion="true"
      >
        <Submenu v-for="(item,index) in menu" :name="index" :key="index">
          <template slot="title">{{item.name}}</template>
          <Menu-item
            :key="ins"
            :name="ins"
            v-for="(chil,ins) in item.children"
            key="chil.id"
          >{{chil.name}}</Menu-item>
        </Submenu>
      </Menu>
      
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      parindex: "",
      theme2: "dark",
      menu: [
        {
          icon: "",
          url: "",
          name: "用户菜单",
          type: "system",
          res: [],
          ops: [],
          children: [
            {
              icon: "",
              url: "/App",
              name: "查看数据",
              res: [],
              ops: [],
              children: []
            },
            {
              icon: "",
              url: "/menu",
              name: "个人资料",
              res: [],
              ops: {},
              children: []
            }
          ]
        }
      ]
    };
  },
  methods: {
    select(key) {
      this.$router.push(this.menu[this.parindex].children[key].url);
    },
    opchange(key) {
      this.parindex = key;
    }
  },
  created() {
    var url = "http://192.168.146.159:3000/userinfo?" + document.cookie;
    this.$http
      .get(url)
      .then(res => {
        
        this.menu = res.data.menus;
      })
      .catch(err => {});
  }
};
</script>
<style>
.menu {
  position: fixed;
  top: 70px;
  z-index: 1;
  width: 12%;
  height: 100%;
  background-color: gray;
}
li {
  height: auto;
}
.a {
  display: none;
}
</style>



