import Vue from 'vue'
import Router from 'vue-router'
import App from '../components/App'
import hellow from '../components/userinfo'
import logout from '../components/logout'
import qrcode from '../components/qrcode'

Vue.use(Router)
let router = new Router({
  mode: 'history',
  routes: [
    {
      path: '/App',
      name: 'App',
      component: App
    },
    {
      path: '/userinfo',
      name: 'hellow',
      component: hellow
    },
    {
      path: '/logout',
      name: 'logout',
      component: logout
    },
    {
      path: '/qrcode',
      name: 'qrcode',
      component: qrcode
    }
  ]
})

export default router



